import React from "react";
import { Link } from "react-router-dom";
import Carnival from "../img/Carnival.jpg";
import Climate from "../img/Climate-Change.jpg";

const Column3 = () => {
  return (
    <div className="column3">
      <div className="rightborder column3a">
        <Link className="column4b col-md-6" to="/entertainment">
          <h4>
            The world's biggest and most spectacular carnival takes place in Rio
          </h4>
          <figure data-category="Festival" className="column3Figure">
            <img src={Carnival} alt="Carnival" className="column3Img" />
          </figure>
        </Link>
      </div>
      <div className="column3b ">
        <Link className="column4b  col-md-8 col-sm-12 col-sm-4" to="/politics">
          <h4>US issues stark warning on climate change</h4>
          <q>Our heads in the sand at our own peril</q>.
          <figure data-category="Global" className="column3Figure">
            <img src={Climate} alt="Climate-Change " className="column3Img" />
          </figure>
        </Link>
      </div>
    </div>
  );
};

export default Column3;
