import React from "react";
import think from "../img/think-big.jpg";
const BigTip = () => {
  return (
    <div>
      <div className="jumbotron">
        <h2>
          How To Thing Big And <span>Win Big</span>
        </h2>
        <figure data-category="Think Big" aria-label="think">
          <img src={think} alt="think big" width="100%" height="500px" />
        </figure>
        <div className="">
          <ul className="">
            <li>
              <h3>Your Imagination</h3>
              <p>
                Giving yourself some time to sit down, think and really imagine
                about what you want to acheive is just your first step of
                accomplishing your imaginary goals.
              </p>
              <q>If you can think it you can do it</q>
            </li>
            <li>
              <h3>Set Goals You will Attain</h3>
              <p>
                For example, if your end goal is to write a best-selling book,
                an example of starting small might be researching the topics or
                genres that are becoming more popular (you don’t want to focus
                on stuff that’s popular now, because chances are things will
                change before your book is finished). If you’re writing a
                speech, starting small might just mean making a list of things
                you want to give a speech about. Once you have your goal broken
                down into steps, rearrange them in the order that you need to
                accomplish them.
              </p>
            </li>
            <div className="">
              <h3>Develop a support system.</h3>
              <li>
                The most important part of accomplishing big goals is realizing
                that you need the help and support of people who care about you.
                Having a support system in place can help you stay motivated.
                Surrounding yourself with supportive people doesn’t mean
                surrounding yourself with people who will never critique your
                plans. You want people who will be honest with you and help you
                to improve your ideas. You can also ask your support system to
                help keep you accountable. Share your deadlines or smaller step
                goals with them and ask them to check in with you.
              </li>
              <li>
                For example, if you’re working on that speech for school, and
                you’re committed to doing something different with it, ask a
                friend to listen to what you have and be honest about how they
                think it’s going. It might be painful to hear that they think
                what you’re trying just doesn’t work, but it will ultimately
                make you better. And getting a new perspective helps you stay
                motivated.
              </li>
              <li>
                Be careful of becoming overly reliant on others, too. If you
                spend too much time getting other people’s opinions and
                following their suggestions, you’ll get really good at helping
                them think big, but won’t do so well thinking big for yourself.
                Criticism can be helpful sometimes, but it is important not to
                base your worth on what other people think. Relying too much on
                other people’s opinions can be self-limiting.
              </li>
            </div>
            <div className="">
              <h3>Don't Be Afraid Of Failure</h3>
              <li>
                If you focus too much on all the ways you might fail, or might
                fall short of your initial goal, you’ll never have the
                motivation to get started. Everyone fails from time to time, and
                it’s okay.{" "}
              </li>
              <li>
                Make a list of the ways that you might fail or fall short, and
                accept that they are distinct possibilities as you work to
                accomplish your big goals. If you do fail, remember that your
                failure doesn’t define you. Assess what led to your failure and
                start again.
              </li>
              <li>
                Keep in mind that success is not a straight line from point A to
                point B. There will be challenges and setbacks along the way.
                Learn about some of the challenges you may face and consider how
                you will handle them.
              </li>
            </div>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default BigTip;
