import React from "react";
import sheba from "../img/sheba.jpg";
import teethsmile from "../img/teethsmile.jpg";
import think from "../img/think-big.jpg";

import { Link } from "react-router-dom";

const Column4 = () => {
  return (
    <div className="column4">
      <div className="column4a">
        <Link to="/reality" className="column4b">
          <figure data-category="History" className="column4Figure">
            <img src={sheba} alt="sheba" className="column4Img" />
          </figure>
          <small>Brief history about</small>
          <h5> Makeda, Queen of Sheba</h5>
        </Link>
      </div>
      <div className=" column4a">
        <Link className="column4b" to="/health-care">
          <figure data-category="Health" className="column4Figure">
            <img
              src={teethsmile}
              alt="Beautiful smile"
              className="column4Img"
            />
          </figure>
          <small>
            You can keep your teeth for your lifetime. Here are some things you
            can do to maintain a healthy mouth and strong teeth.
          </small>
        </Link>
      </div>
      <div className=" column4a">
        <Link className="column4b" to="/stories">
          <figure data-category="BigTip" className="column4Figure">
            <img src={think} alt="Think Big" className="column4Img" />
          </figure>
          <h5>
            How To Thing Big And <span>Win Big</span>
          </h5>
          <small>
            Giving yourself some time to sit down, think and really imagine
            about
          </small>
        </Link>
      </div>
    </div>
  );
};

export default Column4;
