import React from "react";
import Column4 from "../Cardprops/Column4";
import Homerightbar from "../Cardprops/Homerightbar";
import Festival from "../Pages/Festival";
const Entertainment = () => {
  return (
    <div className="container">
      <div className="row">
        <div className=" col-sm-8">
          <Festival />
          <Column4 />
        </div>
        <div className=" col-sm-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default Entertainment;
