import React from "react";
import Dele from "../img/Dele.jpg";
const Football = () => {
  return (
    <div>
      <div className="">
        <h3>
          Mourinho has been reminded of Ferguson's comments on Alli when he
          manager of United.
        </h3>
        <figure>
          <img
            src={Dele}
            alt="Dele"
            width="100%"
            height="400px"
            maxWidth="300px"
          />
        </figure>
        <p>
          It comes after the England international scored a sublime goal against
          Wolfsberger in the Europa League on Wednesday night.
        </p>
        <p>
          The 24-year-old was given a rare chance to impress in a dead rubber
          against the Austrian outfit, with the tie practically over after a 4-1
          win in the first leg. But despite the circumstances, Alli has surely
          given Mourinho something to think about after a stellar display
          including a goal and two assists.
        </p>
        <p>
          The former MK Dons star was creator and finisher for Tottenham's
          opener after just 10 minutes. After spraying the ball out to Matt
          Doherty, the full-back put the cross in that was controlled by Alli in
          tough circumstances. It saw the ball fly up into the air but that
          provided the Englishman with a chance to try the spectacular - which
          he did with a jaw-dropping overhead kick. He then helped double the
          scores with an excellent cross for Carlos Vinicius that gave the
          striker the simple task of nodding in.
        </p>
        <p>
          And Alli was provider again, this time for Gareth Bale, whose
          first-time finish was another example of brilliance. It was a stark
          reminder of Fergie's comments on the midfielder when Mourinho was in
          charge of United. In a meeting with chairman Daniel Levy on the Amazon
          Prime Video documentary All or Nothing, Mourinho admitted: "Sir Alex
          Ferguson gave me only one bit of advice in two and a half years: Buy
          Dele Alli. 'That guy, with that mentality, the way he plays, the
          aggression he has in mind. This guy is the Manchester United player.
          Buy Dele Alli'."
        </p>
      </div>
    </div>
  );
};

export default Football;
