import React, { useState } from "react";
import "../App.css";
import { useAuth } from "../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";
import { Alert } from "react-bootstrap";

export default function Navbar() {
  const [click, setClick] = useState(false);
  const closeMobileMenu = () => setClick(false);
  const handleClick = () => setClick(!click);
  const closeNavbar = () => setClick(false);

  const [error, setError] = useState("");
  //const { logout } = useAuth();
  const history = useHistory();

  //   async function handleLogout() {
  //     setError("");

  //     try {
  //       await logout();
  //       history.push("/login");
  //     } catch {
  //       setError("Failed to log out");
  //     }
  //   }
  //<strong>{currentUser.username}</strong>
  return (
    <div>
      {error && <Alert variant="danger">{error}</Alert>}

      <nav className="w-100 nav">
        <div className="nav_icon" onClick={handleClick}>
          <i className={click ? "fas fa-times" : "fas fa-bars"} />
        </div>
        <div>
          <ul
            className={`ul_container ${click ? "nav-menu active" : "nav-menu"}`}
          >
            <div className="li-flex">
              <Link
                to="/login"
                className="li_home bottom_line"
                onClick={closeNavbar}
              ></Link>
              <Link
                to="/sports"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Sports
              </Link>
              <Link
                to="/stories"
                className=" li_home bottom_line"
                onClick={closeNavbar}
              >
                Stories
              </Link>
              <Link
                to="/reality"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Reality
              </Link>
              <Link
                to="/politics"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Politics
              </Link>
              <Link
                to="/entertainment"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Entertainment
              </Link>
              <Link
                to="/health-care"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Health-Care
              </Link>
              <Link
                to="/"
                onClick={closeNavbar}
                className=" li_home bottom_line"
              >
                Home
              </Link>
            </div>
          </ul>
        </div>
        <div>
          <Link to="/" className="nav_title" onClick={closeMobileMenu}>
            ZimWay
            <i class="fab fa-typo3" />
          </Link>
        </div>
      </nav>
    </div>
  );
}
