import React from "react";
import Column2 from "../Cardprops/Column2";
import Column3 from "../Cardprops/Column3";
import Homerightbar from "../Cardprops/Homerightbar";
import Health from "../Pages/Health";

const SkinCare = () => {
  return (
    <div className="container mt-2">
      <div className="row">
        <div className=" col-sm-8">
          <Health />
          <Column2 />
          <Column3 />
        </div>
        <div className=" col-sm-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default SkinCare;
