import React from "react";
import Column2 from "../Cardprops/Column2";
import Column4 from "../Cardprops/Column4";
import Homerightbar from "../Cardprops/Homerightbar";
import Global from "../Pages/Global";

const Politics = () => {
  return (
    <div className="container">
      <div className="row">
        <div className=" col-sm-8">
          <Global />
          <Column2 />
          <Column4 />
        </div>
        <div className=" col-sm-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default Politics;
