import React from "react";
import CardProps from "./CardProps";
import Dele from "../img/Dele.jpg";
import { Link } from "react-router-dom";
const Column2 = () => {
  return (
    <div className="row">
      <Link to="/sports" className="column2">
        <div className="columnText">
          <h3>
            Mourinho has been reminded of Ferguson's comments on Alli when he
            was the manager of United.
          </h3>

          <p>
            It comes after the England international scored a sublime goal
            against Wolfsberger in the Europa League on Wednesday night.{" "}
            <b className="">Read more....</b>
          </p>
        </div>
        <li className="cards__item">
          <figure data-category="Football" className="columnFigure">
            <img src={Dele} alt="Dele" className="columnImg" />
          </figure>
        </li>
      </Link>
    </div>
  );
};

export default Column2;
