import React from "react";

import { Link } from "react-router-dom";
import CardItem from "../Cardprops/CardItem";

import fog from "../img/fog.jpg";
import Column2 from "./Column2";
import Column3 from "./Column3";
import Column4 from "./Column4";
const HomeCenter = () => {
  return (
    <div>
      <div className="  cards__item__link">
        <CardItem
          src={fog}
          label="Health"
          path="/health-care"
          text="New confirmed cases of COVID-19 recorded 1,204 in Nigeria"
          textp="The Nigeria Centre For Disease Control (NCDC) made this known via
              its verified website. The News Agency of Nigeria (NAN) reports
              that Nigeria recorded 1,145 cases on Dec. 18, 2020....."
        />
        <p>
          {" "}
          On January 5, 2021 new confirmed cases of COVID-19 recorded 1,204 in
          Nigeria, taking the total number of infections to 91,351. Lorem ipsum
          dolor, sit amet consectetur adipisicing elit. Explicabo temporibus
          illo odit voluptas corporis. Tempore quaerat fugiat laborum quo iusto
          qui laboriosam velit dolore eos voluptatibus, praesentium dolorum
          maxime voluptate.
          <Link to="/health-care">
            <b className="Readmore btn-primary"> Read more</b>
          </Link>
        </p>
      </div>
      <div className="">
        <Column2 />
      </div>
      <div className="">
        <Column3 />
      </div>

      <div className="cards__wrapper ">
        <ul className="cards__items d-flex">
          <Column4 />
        </ul>
      </div>
    </div>
  );
};

export default HomeCenter;
