import React from "react";

import { Link } from "react-router-dom";
import CardProps from "../Cardprops/CardProps";
import CardItem from "../Cardprops/CardItem";

import girl from "../img/girl-workout.jpg";
import chat from "../img/chat.png";
import fog from "../img/fog.jpg";

const Homerightbar = () => {
  return (
    <div>
      <ul className=" cards__ite">
        <div className="bottom_border">
          <CardProps
            src={girl}
            label="Body Building"
            path="/"
            text="Best Six Exercises To Build A Perfect Body. Afterburn Workout"
          />
        </div>
      </ul>
      <ul>
        <div className="bottom_border">
          <legend className="textline">SPORTS</legend>
          <Link to="" className="textgray">
            <li>
              <p>
                Mourinho has been reminded of Ferguson's comments on Alli when
                he was the manager of United.
              </p>
            </li>
          </Link>
        </div>
        <div className="bottom_border">
          <legend className="textline">ENTERTAINMENT</legend>
          <Link to="">
            <li className="textgray">
              <p>
                The world's biggest and most spectacular carnival takes place in
                Rio de Janeiro each February, as the famous beaches and iconic
                sights are cast into a supporting role for the epic parades and
                jubilant street parties.
              </p>
            </li>
          </Link>
        </div>
        <div className="bottom_border">
          <legend className="textline">POLITICS</legend>
          <Link to="">
            <li className="textgray">
              <p>
                The United States has warned inaction by world powers on climate
                change is tantamount to a "mutual suicide pact"{" "}
              </p>
            </li>
          </Link>
        </div>
        <div className="bottom_border">
          <legend className="textline">REALITY</legend>
          <Link to="">
            <li className="textgray">
              <p>
                Ethiopia’s 14th-century royal epic, the Kebra Nagast or “Glory
                of Kings,” writes that Makeda was a queen of incredible
                strength.
              </p>
            </li>
          </Link>
        </div>
      </ul>
    </div>
  );
};

export default Homerightbar;
