import React from "react";
import { Link } from "react-router-dom";

const CardProps = (props) => {
  return (
    <div>
      <li className="cards__item">
        <Link to={props.path}>
          <figure data-category={props.label} className="cards__props">
            <img
              src={props.src}
              alt={props.alt}
              className="cards__props__img"
            />
          </figure>
          <div className="cards__props__info">
            <p className="cards__props__text">{props.text}</p>
          </div>
        </Link>
      </li>
    </div>
  );
};

export default CardProps;
