import React from "react";
import Homeleftbar from "../Cardprops/Homeleftbar";

import HomeCenter from "../Cardprops/HomeCenter";
import Homerightbar from "../Cardprops/Homerightbar";

const Home = () => {
  return (
    <div className=" container">
      <div className="row mt-2">
        <div className="column col-md-8">
          <HomeCenter />
        </div>
        <div className=" col-md-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default Home;
