import React from "react";
import Column3 from "../Cardprops/Column3";
import Column4 from "../Cardprops/Column4";
import Homerightbar from "../Cardprops/Homerightbar";
import Football from "../Pages/Football";

const Sports = () => {
  return (
    <div className="container">
      <div className="row">
        <div className=" col-sm-8">
          <Football />
          <hr my="2" />
          <Column4 />
          <Column3 />
        </div>
        <div className=" col-sm-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default Sports;
