import React from "react";
import teethsmile from "../img/teethsmile.jpg";

const Health = () => {
  return (
    <div>
      <div className="jumbotron">
        <h2>Oral Health Tips</h2>
        <h4>What You Can Do to Maintain Good Oral Health?</h4>
        <figure data-category="Health">
          <img
            src={teethsmile}
            alt="Smaile"
            maxWidth="100%"
            width="100%"
            height="500px"
          />
        </figure>
        <p className="lead">
          You can keep your teeth for your lifetime. Here are some things you
          can do to maintain a healthy mouth and strong teeth.
        </p>
        <ul className="">
          <li>Drink fluoridated water and brush with fluoride toothpaste.</li>
          <li>
            Practice good oral hygiene. Brush teeth thoroughly twice a day and
            floss daily between the teeth to remove dental plaque.
          </li>
          <li>
            Visit your dentist at least once a year, even if you have no natural
            teeth or have dentures.
          </li>
          <li>
            Do not use any tobacco products. If you smoke, quit. Limit alcoholic
            drinks.
          </li>
          <li>
            If you have diabetes, work to maintain control of the disease. This
            will decrease risk for other complications, including gum disease.
            Treating gum disease may help lower your blood sugar level.
          </li>
          <li>
            If your medication causes dry mouth, ask your doctor for a different
            medication that may not cause this condition. If dry mouth cannot be
            avoided, drink plenty of water, chew sugarless gum, and avoid
            tobacco products and alcohol.
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Health;
