import React from "react";
import sheba from "../img/sheba.jpg";

const History = () => {
  return (
    <div>
      <div className="">
        <h1 className="bottom_border">African History</h1>
        <h3>Makeda, Queen of Sheba</h3>
        <figure data-category="Afri History">
          <img src={sheba} alt="History" width="100%" height="500px" />
        </figure>
        <p>
          Ethiopia’s 14th-century royal epic, the Kebra Nagast or “Glory of
          Kings,” writes that Makeda was a queen of incredible strength.
        </p>
        <p>
          According to the epic, she survived a battle with the serpent king
          Awre. The serpent king was troubling the northern Ethiopian kingdom of
          Axum. After defeating the serpent king, Makeda became the queen of
          Axum.
        </p>
        <p>
          Makeda is famous for her story with the biblical figure, King Solomon
          of Jerusalem. They had a son named Menelik I (or Ebna la-Hakim),
          meaning “son of the wise.” Their son became the first imperial ruler
          of Ethiopia and the first of a line of Aksûmite kings.
        </p>
        <p>
          According to historians, Makeda and her son brought back the biblical
          Ark of the Covenant to Axum. Through them, the lineage of great East
          African and Nubian kings was born.
        </p>
        <strong>
          She left a legacy as an essential figure in Old Testament history for
          the Ethiopian Orthodox Church.
        </strong>
        <p>
          The first use in the Bible of the word “queen” refers to a Black woman
          (1 Kings 10:1). Her name is alleged to be Makeda, but the Scriptures
          refer to her as the Queen of Sheba or the Queen of the South. Her fame
          was such that 2,000 years after her death, according to
          blacksinthebible.net, Jesus Christ mentioned her long trip and
          referred to her challenge of Solomon’s wisdom, which led to a love
          affair with the wise Jewish king.
        </p>
      </div>
    </div>
  );
};

export default History;
