import React from "react";
import Climate from "../img/Climate-Change.jpg";
const Global = () => {
  return (
    <>
      <div className="">
        <h2>US issues stark warning on climate change</h2>
        <figure data-category="Global Warming">
          <img src={Climate} alt="Climate" width="100%" height="500px" />
        </figure>
        <h6>
          <q>Our heads in the sand at our own peril</q>, US envoy for climate
          warns, as leaders discuss security implications of global warming.
        </h6>
        <p>
          The United States has warned inaction by world powers on climate
          change is tantamount to a <q>mutual suicide pact</q> after countries
          such as China, India, and Russia expressed scepticism on the global
          security threat it posed.
        </p>
        <p>
          John Kerry, the US special presidential envoy for climate, told the UN
          Security Council on Tuesday that the Pentagon has described the
          climate crisis as “a threat multiplier”.
        </p>
        <p>
          The United States has warned inaction by world powers on climate
          change is tantamount to a <q>mutual suicide pact</q> after countries
          such as China, India, and Russia expressed scepticism on the global
          security threat it posed.
        </p>
      </div>
    </>
  );
};

export default Global;
