import React from "react";
import Column2 from "../Cardprops/Column2";
import Column3 from "../Cardprops/Column3";
import Homerightbar from "../Cardprops/Homerightbar";
import History from "../Pages/History";

const Reality = () => {
  return (
    <div className="container">
      <div className="row">
        <div className=" col-sm-8">
          <History />
          <Column3 />
          <Column2 />
        </div>
        <div className=" col-sm-4">
          <Homerightbar />
        </div>
      </div>
    </div>
  );
};

export default Reality;
